<template>
	<view>
		ISO25745认证简介：
		ISO25745扶梯能效认证是国际电梯能效标准，高效节能电梯和自动扶梯也是欧盟委员会智能能源欧洲项目支持的项目。欧盟CE认证主要的电梯能效标准为VDI4707指令。
		ISO25745能效认证标准：
		ISO25745也是评价能源效率的国际性法规，涵盖了电梯、扶梯及人行道产品，包括：
		1.ISO25745-1电梯，自动扶梯和自动人行道的能源性能-第1部分：能源测量和验证
		ISO25745-1规定：
		a）在单个装置上测量电梯，自动扶梯和自动人行道的实际能耗的方法；
		b）对运行中的电梯，自动扶梯和自动人行道进行定期能量验证检查的方法。
		ISO25745-1仅考虑在电梯，自动扶梯或自动人行道生命周期的运营阶段的能源表现。
		2.ISO25745-2电梯，自动扶梯和自动人行道的能源性能-第2部分：电梯的能量计算和分类
		ISO25745-2规定了一种基于测量值，计算或模拟的能源估算方法，该方法基于单个单位的牵引，液压和正驱动举升每年进行一次，并且针对现有的新能源分类系统，以及现代化的牵引，液压和主动驱动举升装置。
		它适用于额定速度大于0.15m/s的客运和货运客运升降机，并且仅考虑升降机生命周期中运行期间的能量性能。对于其他类型的电梯（例如，服务电梯，升降平台等），可以作为参考。
		它不涉及能源方面，能源方面会影响测量，计算和模拟，例如：井道照明；升降车的加热和冷却设备；机房照明；机房供暖，通风和空调；非升降显示系统，闭路电视监控摄像头等；非升降机监控系统（例如建物管理系统等）；电梯群调度对能耗的影响；环境条件；通过电源插座消耗；电梯中包含快速区域的电梯（快速区域不太可能影响平均轿厢负载，但会显着影响平均行进距离）。
		3.ISO25745-3电梯，自动扶梯和自动人行道的能量性能—第3部分：自动扶梯和自动人行道的能量计算和分类
		ISO25745-3指定了用于估算自动扶梯和自动人行道能耗的通用工具，以及用于对现有，现代化或新的自动扶梯和自动人行道进行能源性能分类的一致方法。
		它考虑了自动扶梯和自动人行道生命周期的运营部分的能源性能。它不包括能耗和辅助设备的分类，例如：
		a）除梳状板照明，阶梯式照明和交通灯外的照明（梳状板照明，阶梯式照明和交通灯被认为是设备操作必不可少的，因此未定义为辅助设备）；
		b）冷却和加热以及机房通风；
		c）报警装置和应急电池供应设备等；
		d）环境条件；
		e）通过电源插座消耗。
		可能存在与自动扶梯或自动人行道无关的其他电气负载，但不包括在内。
		ISO25745-3考虑了所有自动扶梯和上升到8m的倾斜移动人行道以及最长60m的水平移动人行道。
		电梯能耗测量的输出信号具有很小的待机值，以及带有非理想谐波信号的高加速度输出的特点，这些都对测量设备提出了很高的要求。测量应该由熟悉测量设备的专业人员执行。另外还必须有一个胜任的维护人员在场以保证安全。
		ISO25745-1中给出了具体的测量过程。扶梯的能效等级由待机和运行的能量需求值确定。根据日常需求的平均待机时间和运行时间来计算待机模式的输出和运行能量需求，然后再除以运行的距离和额定载荷，这样就得到了特定的电梯总能量需求值。
		待机时的能量需求等级：
		输出（W） ≤50 ≤100 ≤200 ≤400 ≤800 ≤1600 >1600
		等级： A B C D E F G
		运行时能量需求等级：
		特定能量消耗（mWh/kgm） ≤0.56 ≤0.84 ≤1.26 ≤1.89 ≤2.80 ≤4.20 >4.20
		等级： A B C D E F G
		电梯能效认证流程：
		1.客户提出评估和认证的申请流程
		2.工程师采集电梯参数
		3.工程师测量电梯的待机功耗
		4.工程师测量评估电梯的运行功耗
		5.工程师对采集的数据进行分析确定能耗等级
		6.出具扶梯的能效证书
	</view>
